@import url('https://fonts.googleapis.com/css2?family=Raleway&display=swap');

.App {
    font-family: 'Raleway', sans-serif;
    padding: 0;
    margin: 0;
}

/* HEADER */

nav {
    width: 100%;
    margin-top: 0;
    overflow: hidden;
    background-image: linear-gradient(-225deg, #FF057C 0%, #8D0B93 50%, #321575 100%);
}

nav ul {
    width: 600px;
    margin: 0 auto;
    padding: 0;
    list-style-type: none;
}

nav li {
    float: left;
    width:200px;
}

nav li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    transition: 0.2s;
}

.selected {
    background: rgba(0, 0, 0, 0.4);
}

nav li a:hover {
    background: rgba(0, 0, 0, 0.6);
}

@media screen and (max-width: 600px) {
    nav ul {
        width: 100%;
    }
    nav li {
        text-align: center;
        float: none;
        width: 100%;
    }
}

/* GENERAL PAGE & INPUT */

.pageContainer {
    width: 720px;
    margin: 0 auto;
}

.pageContainer h1, h2, h3 {
    text-align: center;
    color: #321575;
}

.pageButton {
    margin-bottom: 50px;
    text-align: center;
}

.pageButton button {
    padding: 10px 25px 10px 25px;
    margin: 0 20px 0 20px;
    background: #321575;
    border: none;
    color: white;
    transition: 0.1s;
}

.pageButton button:hover {
    cursor: pointer;
    background: #8D0B93;
}

.search, .sessionTypeDropdown, .slotDropdown {
    text-align: center;
} 

.search p {
    font-size: 20px;
} 

.search input,.sessionTypeDropdown select, .slotDropdown select {
    padding: 10px 20px 10px 20px;
    margin: 10px;
    width: 300px;
}


.wrapper {
    background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
    border: 5px solid #321575;
    border-radius: 25px 0px 25px 0px;
    padding: 10px;
    margin: 25px 0 25px 0;
    overflow: hidden;
}

/* SESSIONS */

.sessionTitle, .authorName {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    display: inline-block;
    text-align: center;
    font-size: 18px;
    padding: 10px 20px 10px 20px;
    color: #321575;
    transition: 0.1s;
}

.sessionTitle, .authorName:hover {
    cursor: pointer;
}

.sessionDetails {
    display: flex;
    /*overflow: hidden;*/
}

.sessionDetails span {
    text-align: center;
    flex: 0 0 33%;
}

.sessionData button {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    
    padding: 10px 20px 10px 20px;
    background-color: #321575;
    outline: none;
    color: white;
    border: none;
    margin-top: 20px;
    transition: 0.1s;
}

.sessionData button:hover {
    background-color: #8D0B93;
    cursor: pointer;
}

/* LOGIN & UPDATE */

.login {
    display: grid;
    width: 350px;
    margin: 0 auto;
}

.login input, button {
    padding: 10px 20px 10px 20px;
    border: 1px solid #321575;
    margin: 5px 10px 5px 10px;
}

.login button {
    background-color: #321575;
    color: white;
    outline: none;
    transition: 0.1s;
}

.login button:hover {
    cursor: pointer;
    background-color: #8D0B93;
    outline: #8D0B93;
}

.errorMsg {
    text-align: center;
}

.logout {
    background: #321575;
    color: white;
    outline: none;
    transition: 0.1s;
}

.logout:hover {
    cursor: pointer;
    background-color: #8D0B93;
}

.update {
    text-align: center;
}

.update textarea {
    width: 80%;
    height: 100px;
    border: 1px solid #321575;
    resize: none;
    padding: 5px;
}

.update button {
    background: #321575;
    color: white;
    outline: none;
    transition: 0.1s;
}

.update button:hover {
    cursor: pointer;
    background-color: #8D0B93;
}

@media screen and (max-width: 950px) {
    .pageContainer {
        width: 80%;
    }
}